@import url(https://fonts.googleapis.com/css?family=Montserrat|Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.component_header {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
	-webkit-transition: height 0.2s ease;
	transition: height 0.2s ease;
}

.component_header h1 {
	font-family: 'Pacifico', cursive;
	color: white;
	font-size: 60px;
	z-index: 31;
}

.component_header h6 {
	font-size: 18px;
	color: #3A606E;
	z-index: 31;
}

.component_header button {
	margin-top: 20px;
	z-index: 31;
}

.component_header.initialized {
	height: 100px;
}

.component_header.initialized h1 {
	font-size: 18px;
	z-index: 31;
}

.component_header .credits {
	position: fixed;
	bottom: 20px;
	left: 50%;
	display: flex;
	-webkit-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
	align-items: center;
	z-index: 31;
}

.component_header .credits a {
	color: white;
	margin-left: 5px;
	font-size: 18px;
}

.component_header .logo {
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 30;
}

.component_header .logo img {
	width: 200px;
}
.component_button {
	height: 30px;
	width: auto;
	padding: 0 10px;
	border: 2px solid white;
	background-color: transparent;
	color: white;
	cursor: pointer;
	-webkit-transition: background-color 0.5s ease, -webkit-transform 0.5s ease;
	transition: background-color 0.5s ease, -webkit-transform 0.5s ease;
	transition: transform 0.5s ease, background-color 0.5s ease;
	transition: transform 0.5s ease, background-color 0.5s ease, -webkit-transform 0.5s ease;
	font-weight: 600;
}

.component_button:hover {
	background-color: white;
	color: #98C1D9;
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	outline: 0;
}

.component_button:focus {
	outline:0;
}

button:disabled {
	cursor: not-allowed;
}
.component_flyout {
	position: absolute;
	top: 0;
	left: -400px;
	height: 100%;
	width: 100%;
	-webkit-transition: background-color 0.5s ease, left 0.5s ease;
	transition: background-color 0.5s ease, left 0.5s ease;;
	background-color: transparent;
	z-index: 999;
	text-align: left;
	pointer-events: none;
	overflow: auto;
}

.component_flyout h6 {
	color: white;
	font-style: italic;
	-webkit-animation: pulse 5s infinite;
	        animation: pulse 5s infinite;
	font-weight: bold;
}

.component_flyout .search_criteria .component_input:nth-child(2) {
	margin-bottom: 40px;
}

.flyout_content {
	height: 100%;
	width: 400px;
	background-color: #3A606E;
	overflow: hidden;
	padding: 20px;
}

.component_flyout.active {
	background-color: rgba(255,255,255, 0.8);
}

.component_flyout.active {
	left: 0;
	pointer-events: auto;
}

.flyout_content .search_criteria {
	padding: 50px 0 20px 0;
}

@-webkit-keyframes pulse {
	0% {-webkit-transform: scale(1);transform: scale(1);}
	10% {-webkit-transform: scale(1.05);transform: scale(1.05);}
	20% {-webkit-transform: scale(1);transform: scale(1);}
	100% {-webkit-transform: scale(1);transform: scale(1);}
}

@keyframes pulse {
	0% {-webkit-transform: scale(1);transform: scale(1);}
	10% {-webkit-transform: scale(1.05);transform: scale(1.05);}
	20% {-webkit-transform: scale(1);transform: scale(1);}
	100% {-webkit-transform: scale(1);transform: scale(1);}
}
.component_input {
	-webkit-appearance: none;
    max-width: 300px;
    width: 80%;
    border: 0;
    padding: 12px 0;
    height: 48px;
    font-size: 16px;
    font-weight: 500;
    border-bottom: 2px solid white;
    background-color: transparent;
    color: white;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}

.component_input:hover {
	background-color: rgba(39, 40, 56, 0.2);
}

.component_input:focus {
	background: none;
	outline: none;
}

.component_calendar {
	height: calc(100% - 150px);
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.component_calendar img {
	max-width: calc(50vw - 40px);
	max-height: calc(100vh - 170px);
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	position: relative;
}

.img_wrapper {
	position: relative;
	-webkit-transition: -webkit-transform 0.5s ease;
	transition: -webkit-transform 0.5s ease;
	transition: transform 0.5s ease;
	transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}

.img_wrapper .calendar_content {
	z-index: 998;
	position: absolute;
	left: 100%;
	top: 0;
	background-color: white;
	width: 100%;
	height: 100%;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
	-webkit-transition: width 0.5s ease;
	transition: width 0.5s ease;
}

.img_wrapper .calendar_content .calendar_days {
	display: grid;
	grid-template-columns: repeat(7, minmax(10px, 1fr));
	grid-template-rows: repeat(6, minmax(20px, 40px));
	height: 100%;
	padding: 20px;
}

.img_wrapper .calendar_content .calendar_days .day.today {
	font-weight: 900;
	color: #98C1D9;
}

.img_wrapper .calendar_content .calendar_days .day.week {
	font-weight: 900;
	color: #3A606E;
}

.img_wrapper .calendar_month_year {
	position: absolute;
	left: 50%;
	top: 20px;
	-webkit-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);
	color: white;
	font-size: 28px;
	font-weight: bold;
	z-index: 999;
	border: 3px solid white;
	padding: 3px 8px;
}

.container_content {
	height: 100%;
	width: 100%;
	position: relative;
}

.container_content .pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px 0;
}

.container_content .pagination button:first-child {
	margin-right: 30px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
}

.App {
  text-align: center;
  background-color: #98C1D9;
  height: 100vh;
  width: 100%;
}

